<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Pegawai</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Pegawai
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="pegawais" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["id"]'>
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["Nama"] }}</td>
                                <td class="title">{{ item["No Induk"] }}</td>
                                <td>
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click='deleteData(item["id"])'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                    
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog" persistent max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Pegawai</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4">
                                <h2 class="mt-2">Nama</h2>
                            </v-col>
                            <v-col cols="8">
                                <v-text-field dense single-line outlined label="Nama Pegawai" class="title mr-4" v-model="form.nama" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="4">
                                <h2 class="mt-2">No Induk</h2>
                            </v-col>
                            <v-col cols="8">
                                <v-text-field dense single-line outlined label="No Induk" class="title mr-4" v-model="form.noInduk" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <!-- <small>*indicates required field</small> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                    <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                dialog: false,
                keyword: '',
                headers: [
                    {
                        text: 'No',
                        value: 'no',
                        class: 'title',
                    },
                    {
                        text: 'Nama',
                        value: 'Nama',
                        class: 'title',
                    },
                    {
                        text: 'No Induk',
                        value: 'No Induk',
                        class: 'title',
                    },
                    {
                        text: 'Aksi',
                        value: '',
                        class: 'title',
                    },
                ],
                pegawais: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                form: {
                    nama: '',
                    noInduk: '',
                },
                pegawai: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
            }
        },
        methods: {
            async getData() {
                var uri = this.$apiUrl + 'pegawais'
                var res = await this.$http.get(uri)
                this.pegawais = res.data
            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
            sendData() {
                this.pegawai.append('nama', this.form.nama);
                this.pegawai.append('noInduk', this.form.noInduk);
                var uri = this.$apiUrl + 'pegawais'
                this.load = true
                this.$http.post(uri, this.pegawai).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.close();
                    this.Activity('Melakukan Penambahan Data Pegawai');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                })
            },
            updateData() {
                this.pegawai.append('nama', this.form.nama);
                this.pegawai.append('noInduk', this.form.noInduk);
                var uri = this.$apiUrl + 'pegawais/' + this.updatedId;
                this.load = true
                this.$http.post(uri, this.pegawai).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.close()
                    this.Activity('Melakukan Perubahan Data Pegawai');
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })
            },
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.dialog = true;
                this.form.nama = item["Nama"];
                this.form.noInduk = item["No Induk"];
                this.updatedId = item["id"];
            },
            deleteData(deleteId) { //mengahapus data
                var uri = this.$apiUrl +
                    'pegawais/' + deleteId; //data dihapus berdasarkan id
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    this.Activity('Melakukan Delete Data Pegawai');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialog = false
                this.resetForm()
                this.getData()
            },
            resetForm() {
                this.form= {
                    nama: '',
                    noInduk: '',
                }
                this.statusUpdate = true
            }
        },
        mounted() {
            this.getData();
        },
    }
</script>